.detail {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
    display: flex;
	justify-content: center;
	align-items: center;
	color: white;
	z-index: 1000;
}

.detail-content {
	background-color:white;
	padding: 20px;
	border-radius: 10px;
	text-align: center;
	position: relative;
	max-width: 70vw;
	width: 90%;
    border: 2px solid purple;
    font-family: 'Times New Roman', Times, serif;
   
 
}

.close {
	position: absolute;
	top: 10px;
	right: 10px;
	font-size: 24px;
	cursor: pointer;
}
.det-img{
    height: 48vh;
    width: 39vw;
    margin-top: 1em;
    margin: 1.4em;

}
.information{
    margin-left: 2.4em;
}
.data{
    display: flex;
    align-items: center;
    justify-content: left;
    color: green;
}
.description{
    padding: 0.4em;
}
