*{
    

   
}
.App{
    
    
    font-size: 2em;
    box-sizing: border-box;
    color: black;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-style: italic;
    color: black;
}
header{
    text-align: center;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: bolder;
    font-size: larger;
    height:10vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    
 
    
    
}
.container{
   display:grid;
   grid-template-columns: repeat(3, 1fr);
   padding: 2em;
   height: auto;
   font-size: 0.8em;
   width: auto;
   border-bottom: 2px solid purple;
   margin-top: 2em;
}
img{
    height: 35vh;
    width: 27vw;

}
.item{
    height: auto;
    width: auto;
    background-color:purple;
    padding: 1em;
    margin: 1em;
    border: 1px solid white;
    margin: 1em;
    text-align: center;

}
.footer{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column ;
    font-size: 0.7em;
    padding: 2em;
    margin: 2em;
 
   
  margin-top: 5em;

  padding: 2em;
    
  
    height: 6vh;
   }
.github-icon{
    height: 10vh;
    width: 13vw;
    margin-left: 2.8em;


}
  
  
